.searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 15px;
  padding-bottom: 15px;
  color: #fff;
  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.button {
  position: absolute;
  top: 22px;
  border: 0;
  background-color: transparent;
  cursor: pointer;
  opacity: 0.6;
}

.button:hover {
  opacity: 1;
}

.input {
  display: inline-block;
  width: 320px;
  font: inherit;
  font-size: 22px;
  border: none;
  outline: none;
  padding: 5px 4px 8px 45px;
  border-radius: 2px;
}

.input::placeholder {
  font: inherit;
  font-size: 18px;
}
